











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Player extends Vue {
	@Prop() private isPlaying: boolean = false;

	public togglePlayer() {
		this.$emit('togglePlayer');
	}
	public goBack() {
		this.$emit('goBack');
	}
	public goForward() {
		this.$emit('goForward');
	}
}



